_variables.scss'\n\n+theme('v-toolbar.v-sheet') using ($material)\n  background-color: map-get($material, 'toolbar')\n\n\n// Sheet\n+sheet(v-toolbar, $toolbar-elevation, $toolbar-border-radius, $toolbar-shaped-border-radius)\n\n// Block\n.v-toolbar\n  contain: layout\n  display: block\n  flex: 1 1 auto\n  max-width: 100%\n  transition: $toolbar-transition\n  position: relative\n\n  +elevation(4)\n\n  .v-input\n    padding-top: 0\n    margin-top: 0\n\n// Element\n.v-toolbar__content,\n.v-toolbar__extension\n  padding: $toolbar-content-padding-y $toolbar-content-padding-x\n\n  .v-btn.v-btn--icon.v-size--default\n    height: $toolbar-btn-icon-size\n    width: $toolbar-btn-icon-size\n\n  > .v-btn.v-btn--icon\n    &:first-child\n      +ltr()\n        margin-left: -$toolbar-btn-icon-size / 4\n\n      +rtl()\n        margin-right: -$toolbar-btn-icon-size / 4\n\n      + .v-toolbar__title\n        +ltr()\n          padding-left: $toolbar-title-padding\n\n        +rtl()\n          padding-right: $toolbar-title-padding\n\n    &:last-child\n      +ltr()\n        margin-right: -$toolbar-btn-icon-size / 4\n\n      +rtl()\n        margin-left: -$toolbar-btn-icon-size / 4\n\n  > .v-tabs\n    height: inherit\n    margin-top: -$toolbar-content-padding-y\n    margin-bottom: -$toolbar-content-padding-y\n\n    > .v-slide-group.v-tabs-bar\n      background-color: inherit\n      height: inherit\n\n    &:first-child\n      margin-left: -$toolbar-content-padding-x\n\n    &:last-child\n      margin-right: -$toolbar-content-padding-x\n\n.v-toolbar__content,\n.v-toolbar__extension\n  align-items: center\n  display: flex\n  position: relative\n  z-index: 0\n\n.v-toolbar__image\n  border-radius: inherit\n  position: absolute\n  top: 0\n  bottom: 0\n  width: 100%\n  z-index: 0\n  contain: strict\n\n  .v-image\n    border-radius: inherit\n\n.v-toolbar__items\n  display: flex\n  height: inherit\n\n  > .v-btn\n    border-radius: 0\n    height: 100% !important\n    max-height: none\n\n.v-toolbar__title\n  font-size: 1.25rem\n  line-height: 1.5\n  overflow: hidden\n  text-overflow: ellipsis\n  white-space: nowrap\n\n// Modifier\n.v-toolbar.v-toolbar--absolute\n  position: absolute\n  top: 0\n  z-index: 1\n\n.v-toolbar.v-toolbar--bottom\n  top: initial\n  bottom: 0\n\n.v-toolbar.v-toolbar--collapse\n  .v-toolbar__title\n    white-space: nowrap\n\n.v-toolbar.v-toolbar--collapsed\n  max-width: $toolbar-collapsed-max-width\n  overflow: hidden\n\n  +ltr()\n    border-bottom-right-radius: $toolbar-collapsed-border-radius\n\n  +rtl()\n    border-bottom-left-radius: $toolbar-collapsed-border-radius\n\n  .v-toolbar__title,\n  .v-toolbar__extension\n    display: none\n\n.v-toolbar--dense\n  .v-toolbar__content,\n  .v-toolbar__extension\n    padding-top: 0\n    padding-bottom: 0\n\n// @deprecated\n// TODO: Remove in v3\n.v-toolbar--flat\n  +elevation(0, true)\n\n.v-toolbar--floating\n  display: inline-flex\n\n.v-toolbar--prominent\n  .v-toolbar__content\n    align-items: flex-start\n\n  .v-toolbar__title\n    font-size: 1.5rem\n    padding-top: $toolbar-prominent-padding\n\n  &:not(.v-toolbar--bottom)\n    .v-toolbar__title\n      align-self: flex-end\n      padding-bottom: $toolbar-prominent-padding\n      padding-top: 0\n","@mixin paper ($elevation, $radius, $shaped-radius)\n  +radius($radius)\n\n  &:not(.v-sheet--outlined)\n    +elevation($elevation)\n\n  @if ($shaped-radius)\n    &.v-sheet--shaped\n      +radius($shaped-radius)\n\n\n@mixin sheet ($component, $elevation, $radius, $shaped-radius)\n  .v-sheet.#{$component}\n    +paper($elevation, $radius, $shaped-radius)\n","@mixin radius($r, $important: false)\n  // Key exists within the $rounded variable\n  @if (map-has-key($rounded, $r))\n    border-radius: map-get($rounded, $r) if($important, !important, null)\n  @else\n    border-radius: $r if($important, !important, null)\n","@mixin elevation($z, $important: false)\n  box-shadow: map-get($shadow-key-umbra, $z), map-get($shadow-key-penumbra, $z), map-get($shadow-key-ambient, $z) if($important, !important, null)\n\n@mixin elevationTransition($duration: 280ms, $easing: cubic-bezier(0.4, 0, 0.2, 1))\n  transition: box-shadow $duration $easing\n","@import '../../styles/styles.sass';\n\n$toolbar-btn-icon-size: 48px !default;\n$toolbar-content-padding-y: 4px !default;\n$toolbar-content-padding-x: 16px !default;\n$toolbar-elevation: 4 !default;\n$toolbar-border-radius: 0 !default;\n$toolbar-shaped-border-radius: map-get($rounded, 'xl') $toolbar-border-radius !default;\n$toolbar-title-padding: 20px !default;\n$toolbar-transition: 0.2s map-get($transition, 'fast-out-slow-in') transform,\n                     0.2s map-get($transition, 'fast-out-slow-in') background-color,\n                     0.2s map-get($transition, 'fast-out-slow-in') left,\n                     0.2s map-get($transition, 'fast-out-slow-in') right,\n                     280ms map-get($transition, 'fast-out-slow-in') box-shadow,\n                     0.25s map-get($transition, 'fast-out-slow-in') max-width,\n                     0.25s map-get($transition, 'fast-out-slow-in') width !default;\n$toolbar-collapsed-max-width: 112px !default;\n$toolbar-collapsed-border-radius: 24px !default;\n$toolbar-prominent-padding: 6px !default;\n","@mixin rtl()\n  .v-application--is-rtl &\n    @content\n\n@mixin ltr()\n  .v-application--is-ltr &\n    @content\n"],"sourceRoot":""}]);
