right: $chip-pill-avatar-margin-before\n\n      &--right\n        margin-left: $chip-pill-avatar-margin-before\n\n.v-chip--label\n  border-radius: $chip-label-border-radius !important\n\n.v-chip.v-chip--outlined\n  border-width: $icon-outlined-border-width\n  border-style: solid\n\n  &.v-chip--active:before\n    opacity: $chip-outlined-active-opacity\n\n  .v-icon\n    color: inherit\n\n  // Needs increased specificity\n  &.v-chip.v-chip\n    background-color: transparent !important\n\n// Needs increased specificity\n// to overwrite theme color\n.v-chip.v-chip--selected\n  background: transparent\n\n  &:after\n    opacity: $chip-selected-opacity\n\n.v-chip\n  @each $name, $size in $icon-sizes\n    &.v-size--#{$name}\n      border-radius: #{map-get($size, 'height') / 2}px\n      font-size: #{map-get($size, 'font-size')}px\n      height: #{map-get($size, 'height')}px\n","@mixin theme ($component)\n  .theme--light.#{$component}\n    @content($material-light)\n  .theme--dark.#{$component}\n    @content($material-dark)\n","@mixin states ($material)\n  &:hover::before\n    opacity: map-deep-get($material, 'states', 'hover')\n\n  &:focus::before\n    opacity: map-deep-get($material, 'states', 'focus')\n\n  &--active\n    +active-states($material)\n\n@mixin active-states ($material)\n  &:hover::before,\n  &::before\n    opacity: map-deep-get($material, 'states', 'activated')\n\n  &:focus::before\n    opacity: map-deep-get($material, 'states', 'pressed')\n","@import '../../styles/styles.sass';\n@import '../../styles/tools/_functions.sass';\n\n$chip-avatar-size: 24px !default;\n$chip-close-size: 18px !default;\n$chip-icon-margin-after: 6px !default;\n$chip-icon-margin-before: -6px !default;\n$chip-icon-right-margin-after: -4px !default;\n$chip-icon-right-margin-before: 8px !default;\n$chip-icon-size: 24px !default;\n$chip-label-border-radius: $border-radius-root !default;\n$chip-link-focus-opacity: 0.32 !default;\n$chip-pill-avatar-margin-after: 8px !default;\n$chip-pill-avatar-margin-before: -12px !default;\n$chip-pill-avatar-size: 32px !default;\n$chip-pill-filter-margin: 0 16px 0 0 !default;\n$chip-transition-duration: 0.28s !default;\n$chip-transition-fn: map-get($transition, 'fast-out-slow-in') !default;\n$icon-outlined-border-width: thin !default;\n$chip-line-height: 20px !default;\n$chip-padding: 0 12px !default;\n$chip-white-space: nowrap !default;\n$chip-disabled-opacity: 0.4 !default;\n$chip-filter-max-width: 24px !default;\n$chip-outlined-active-opacity: 0.08 !default;\n$chip-selected-opacity: 0.28 !default;\n$icon-sizes: () !default;\n$icon-sizes: map-deep-merge(\n  (\n    'x-small': (\n      'font-size': 10,\n      'height': 16\n    ),\n    'small': (\n      'font-size': 12,\n      'height': 24\n    ),\n    'default': (\n      'font-size': 14,\n      'height': 32\n    ),\n    'large': (\n      'font-size': 16,\n      'height': 54\n    ),\n    'x-large': (\n      'font-size': 18,\n      'height': 66\n    )\n  ),\n  $icon-sizes\n);\n","@mixin rtl()\n  .v-application--is-rtl &\n    @content\n\n@mixin ltr()\n  .v-application--is-ltr &\n    @content\n","@mixin elevation($z, $important: false)\n  box-shadow: map-get($shadow-key-umbra, $z), map-get($shadow-key-penumbra, $z), map-get($shadow-key-ambient, $z) if($important, !important, null)\n\n@mixin elevationTransition($duration: 280ms, $easing: cubic-bezier(0.4, 0, 0.2, 1))\n  transition: box-shadow $duration $easing\n"],"sourceRoot":""}]);
